*, :after, :before {
  box-sizing: border-box;
  border: none;
  margin: 0;
  padding: 0;
}

html, body {
  height: 100%;
  width: 100%;
}

.work {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.work__title {
  max-width: 1170px;
  margin: 0 auto;
  padding: 80px 15px;
}

.work__title__italic {
  color: #2d3033;
  font-family: Kaushan Script, cursive;
  font-size: 30px;
  font-weight: 400;
  line-height: 44px;
}

.work__title__bold {
  text-transform: uppercase;
  color: #2d3033;
  margin: 38px auto;
  font-family: Raleway, sans-serif;
  font-size: 36px;
  font-weight: 900;
  line-height: 42px;
}

.work__title__separator {
  width: 170px;
  border: none;
  border-top: 4px solid #f38281;
  margin: 44px auto 38px;
}

.work__title__text {
  color: #000;
  font-family: Raleway, sans-serif;
  font-size: 18px;
  font-weight: 400;
  line-height: 21px;
}

.work__gallery {
  max-width: 1510px;
  grid-template-rows: repeat(2, 378px);
  grid-template-columns: repeat(4, 1fr);
  grid-template-areas: "a b c d"
                       "e f c h";
  margin: 0 auto;
  padding: 0 15px;
  display: grid;
}

.work__gallery__img:nth-child(1) {
  grid-area: a;
}

.work__gallery__img:nth-child(2) {
  grid-area: b;
}

.work__gallery__img:nth-child(3) {
  grid-area: c;
}

.work__gallery__img:nth-child(4) {
  grid-area: d;
}

.work__gallery__img:nth-child(5) {
  grid-area: e;
}

.work__gallery__img:nth-child(6) {
  grid-area: f;
}

.work__gallery__img:nth-child(7) {
  grid-area: h;
}

